<template>
  <div>
    <b-navbar class="b-navbar" toggleable="md" type="dark" variant="secondary">
      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav class="b-navbar-nav">
          <b-navbar-brand
            @click="new_array(slider.value)"
            class="b-navbar-brand"
            :class="[
              {'disabled':(isSorting === true)},
            ]"
          >Generate New Array</b-navbar-brand>
        </b-navbar-nav>
        <span class="seperator"></span>

        <b-navbar-nav class="b-navbar-nav">
          <b-row class="d-flex justify-content-center">
            <b-col>
              <span
                style="color: #F8FFFF"
                :class="[
              {'disabled':(isSorting === true)},
            ]"
              >Change Array Size and Sorting Speed</span>
            </b-col>
            <b-col>
              <div>
                <VueSlideBar
                  v-model="slider.value"
                  :data="slider.data"
                  :processStyle="{ backgroundColor: this.sliderBackgroundColor }"
                  @callbackRange="callbackRange"
                  :draggable="sliderDraggable"
                  :is-disabled="sliderIsDisabled"
                  :show-tooltip="sliderShowTooltip"
                >
                  <template slot="tooltip" slot-scope="tooltip"></template>
                </VueSlideBar>
              </div>
            </b-col>
          </b-row>
        </b-navbar-nav>
        <span class="seperator"></span>

        <b-navbar-nav class="b-navbar-nav">
          <b-navbar-brand
            @click="select_methods(1)"
            class="b-navbar-brand"
            :class="[
              {'active':(selected_methods === 1 && isSorting === false)},
              {'disabled':(isSorting === true)},
            ]"
          >Merge Sort</b-navbar-brand>
          <b-navbar-brand
            @click="select_methods(2)"
            class="b-navbar-brand"
            :class="[
              {'active':(selected_methods === 2 && isSorting === false)},
              {'disabled':(isSorting === true)},
            ]"
          >Quick Sort</b-navbar-brand>
          <b-navbar-brand
            @click="select_methods(3)"
            class="b-navbar-brand"
            :class="[
              {'active':(selected_methods === 3 && isSorting === false)},
              {'disabled':(isSorting === true)},
            ]"
          >Heap Sort</b-navbar-brand>
          <b-navbar-brand
            @click="select_methods(4)"
            class="b-navbar-brand"
            :class="[
              {'active':(selected_methods === 4 && isSorting === false)},
              {'disabled':(isSorting === true)},
            ]"
          >Bubble Sort</b-navbar-brand>
        </b-navbar-nav>
        <span class="seperator"></span>

        <b-navbar-nav class="b-navbar-nav">
          <b-navbar-brand
            @click="sort()"
            class="b-navbar-brand"
            :class="[
              {'disabled':(isSorting === true)},
            ]"
          >Sort</b-navbar-brand>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>

    <b-container class="b-container">
      <b-row class="d-flex justify-content-center">
        <span
          v-for="(col, index) in cols"
          :key="index"
          class="sort-column text-center"
          :style="{height: col + 'px', width: width +'px'}"
          :class="[
            {'active-col':(active_cols.includes(index))},
            {'pivot-col':(pivot_cols.includes(index))},
            {'red-col':(red_cols.includes(index))},
            {'green-col':(green_cols.includes(index) || finishedSorting)}
          ]"
        ></span>
      </b-row>
      <b-row class="d-flex justify-content-center fixed-bottom">
        <b-navbar-brand class="b-navbar-brand">Made by Nam</b-navbar-brand>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import { getMergeSortAnimations } from "../sortingAlgo/mergeSort";
import VueSlideBar from "vue-slide-bar";

export default {
  name: "Sorting",
  components: {
    VueSlideBar
  },
  data() {
    return {
      selected_methods: 1,
      sorting_time: 0,
      width: 0,

      rangeValue: {},
      slider: {
        value: 150,
        data: [10, 20, 30, 40, 50, 60, 150]
      },
      sliderDraggable: true,
      sliderIsDisabled: false,
      sliderBackgroundColor: "#d8d8d8",
      sliderShowTooltip: true,

      cols: [],
      active_cols: [],
      pivot_cols: [],
      red_cols: [],
      green_cols: [],
      finishedSorting: false,
      isSorting: false
    };
  },
  watch: {
    slider: {
      handler: function(val) {
        if (this.isSorting === false) {
          this.new_array(val.value);
          if (val.value === 10) {
            this.width = 50;
            this.sorting_time = 400;
          } else if (val.value === 20) {
            this.width = 30;
            this.sorting_time = 200;
          } else if (val.value === 30) {
            this.width = 15;
            this.sorting_time = 100;
          } else if (val.value === 40) {
            this.width = 10;
            this.sorting_time = 50;
          } else if (val.value === 50) {
            this.width = 7;
            this.sorting_time = 20;
          } else if (val.value === 60) {
            this.width = 5;
            this.sorting_time = 10;
          } else if (val.value === 150) {
            this.width = 3;
            this.sorting_time = 5;
          }
        }
      },
      deep: true,
      immediate: true
    },
    isSorting: {
      handler: function() {
        if (this.isSorting === true) {
          (this.sliderDraggable = false),
            (this.sliderIsDisabled = true),
            (this.sliderShowTooltip = false),
            (this.sliderBackgroundColor = "#e62510");
        } else {
          (this.sliderDraggable = true),
            (this.sliderIsDisabled = false),
            (this.sliderShowTooltip = true),
            (this.sliderBackgroundColor = "#d8d8d8");
        }
      },
      immediate: true
    }
  },
  mounted() {
    this.new_array(this.slider.value);
  },
  methods: {
    callbackRange(val) {
      if (this.isSorting === false) {
        this.rangeValue = val;
      }
    },
    new_array(amount) {
      if (this.isSorting === false) {
        (this.cols = []),
          (this.active_cols = []),
          (this.pivot_cols = []),
          (this.red_cols = []),
          (this.green_cols = []),
          (this.finishedSorting = false);
        for (let i = 0; i < amount; i++) {
          this.cols.push(this.randomInt(100, 550));
        }
      }
    },
    randomInt(min, max) {
      return Math.floor(Math.random() * (max - min + 1) + min);
    },
    select_methods(method) {
      this.selected_methods = method;
    },
    sort() {
      if (this.finishedSorting === false && this.isSorting === false) {
        this.isSorting = true;
        if (this.selected_methods === 1) {
          this.mergeSort(this.cols).then(() => {
            this.finishedSorting = true;
            this.isSorting = false;
          });
        } else if (this.selected_methods === 2) {
          this.quickSort(this.cols, 0, this.cols.length - 1).then(() => {
            this.finishedSorting = true;
            this.isSorting = false;
          });
        } else if (this.selected_methods === 3) {
          this.heapSort(this.cols).then(() => {
            this.finishedSorting = true;
            this.isSorting = false;
          });
        } else if (this.selected_methods === 4) {
          this.bubbleSort(this.cols).then(() => {
            this.finishedSorting = true;
            this.isSorting = false;
          });
        } else {
          alert("Choose a method!");
        }
      }
    },
    async bubbleSort(arr) {
      for (let i = 0; i < arr.length - 1; i++) {
        for (let j = 0; j < arr.length - i; j++) {
          this.red_cols = [];
          this.green_cols = [];
          this.active_cols = [j, j + 1];

          await this.sleep(this.sorting_time);

          if (arr[j] > arr[j + 1]) {
            this.red_cols = [j, j + 1];
            let temp = arr[j];
            arr[j] = arr[j + 1];
            arr[j + 1] = temp;

            await this.sleep(this.sorting_time);
          } else {
            this.green_cols = [j, j + 1];
            await this.sleep(this.sorting_time);
          }
          this.$forceUpdate();
        }
      }
    },
    async quickSort(arr, start, end) {
      if (start >= end) {
        return;
      }
      let index = await this.partition(arr, start, end);
      Promise.all([
        await this.quickSort(arr, start, index - 1),
        await this.quickSort(arr, index + 1, end)
      ]);
    },
    async partition(arr, start, end) {
      let pivotIndex = start,
        pivotValue = arr[end];

      this.pivot_cols = [end];

      for (let i = start; i < end; i++) {
        this.red_cols = [];
        this.green_cols = [];
        this.active_cols = [i];

        await this.sleep(this.sorting_time);

        if (arr[i] < pivotValue) {
          this.active_cols = [];
          this.green_cols = [];
          this.red_cols = [i, pivotIndex];

          await this.swap(arr, i, pivotIndex);
          await this.sleep(this.sorting_time);
          pivotIndex++;
        } else {
          this.green_cols = [i];
          await this.sleep(this.sorting_time);
        }

        this.$forceUpdate();
      }
      await this.swap(arr, pivotIndex, end);

      return pivotIndex;
    },
    async swap(arr, a, b) {
      let temp = arr[a];
      arr[a] = arr[b];
      arr[b] = temp;
    },
    async heapSort(arr) {
      let arrLength = arr.length;

      for (let i = Math.floor(arrLength / 2); i >= 0; i -= 1) {
        await this.maxHeap(arr, i, arrLength);
      }

      for (let i = arrLength - 1; i > 0; i--) {
        await this.swapHeap(arr, 0, i);

        arrLength--;

        await this.maxHeap(arr, 0, arrLength);
      }
      return arr;
    },
    async maxHeap(arr, i, arrLength) {
      const left = 2 * i + 1;
      const right = 2 * i + 2;
      let max = i;

      if (left < arrLength && arr[left] > arr[max]) {
        max = left;
      }

      if (right < arrLength && arr[right] > arr[max]) {
        max = right;
      }

      if (max != i) {
        await this.swapHeap(arr, i, max);

        await this.maxHeap(arr, max, arrLength);
      }
    },
    async swapHeap(arr, a, b) {
      this.active_cols = [a, b];
      await this.sleep(this.sorting_time);

      let temp = arr[a];
      arr[a] = arr[b];
      arr[b] = temp;

      this.active_cols = [];
      this.red_cols = [a, b];
      await this.sleep(this.sorting_time);
      this.red_cols = [];
    },
    async mergeSort(array) {
      const animations = await getMergeSortAnimations(array);
      for (let i = 0; i < animations.length; i++) {
        const arrayBars = document.getElementsByClassName("sort-column");
        const isColorChange = i % 3 !== 2;
        if (isColorChange) {
          const [barOneIndex, barTwoIndex] = animations[i];
          const barOneStyle = arrayBars[barOneIndex].style;
          const barTwoStyle = arrayBars[barTwoIndex].style;
          const color = i % 3 === 0 ? "#ca44c4" : "#2c3e50";

          await this.sleep(this.sorting_time);
          barOneStyle.backgroundColor = color;
          barTwoStyle.backgroundColor = color;
        } else {
          await this.sleep(this.sorting_time);
          const [barOneIndex, newHeight] = animations[i];
          const barOneStyle = arrayBars[barOneIndex].style;
          barOneStyle.height = `${newHeight}px`;
        }
      }
    },
    sleep(ms) {
      return new Promise(resolve => setTimeout(resolve, ms));
    }
  }
};
</script>

<style scoped>
.b-navbar {
  height: 90px;
}
.b-navbar-nav {
  margin: 2%;
}
.b-navbar-brand {
  cursor: pointer;
  font-size: 100%;
}
.seperator {
  width: 5px;
  height: 50px;
  background-color: #2c3e50 !important;
}
.sort-column {
  margin: 0 2px;
  background-color: #34495e;
}
.active {
  color: #ca44c4 !important;
}
.disabled {
  cursor: default;
  color: #e62510 !important;
}
.active-col {
  background-color: #ca44c4 !important;
}
.pivot-col {
  background-color: #f1c40f !important;
}
.red-col {
  background-color: #e74c3c !important;
}
.green-col {
  background-color: #2ecc71 !important;
}
</style>
